import { forwardRef } from 'react';
import ReactSelect, { Props } from 'react-select';

type Option = { label: string; value: string };

export type MultiSelectProps = Props<Option, true>;

export const MultiSelect = forwardRef<ReactSelect<Option, true>, MultiSelectProps>((props, ref) => {
	return <ReactSelect classNamePrefix='react-select' isMulti {...props} ref={ref} />;
});
