import { forwardRef } from 'react';

import { NumberInput, Select, Switch, Textarea, TextInput, MultiSelect } from '@wp-plugins/adapters';

import type { RenderFieldProps, FieldType, DataShape } from '../types';
import { AdapterPropsMap } from './types';
import { Radio } from './Radio';
import { MultiCheck } from './MultiCheck';
import { MultiSelectAsync } from './MultiSelectAsync';
import { TextWithButton } from './TextWithButton';
import { ColorPicker } from './ColorPicker';
import { Hidden } from './Hidden';

export const adapterMap: {
	[K in FieldType]: React.ComponentType<AdapterPropsMap[K]>;
} = {
	colorpicker: ColorPicker,
	group: null,
	hidden: Hidden,
	multicheck: MultiCheck,
	multiselect: MultiSelect,
	'multiselect.async': MultiSelectAsync,
	number: NumberInput,
	radio: Radio,
	select: Select,
	switch: Switch,
	text: TextInput,
	textarea: Textarea,
	'text.button': TextWithButton,
};

const DefaultComponent = () => null;

const nonRefTypes: Array<FieldType> = ['colorpicker', 'radio', 'multicheck', 'multiselect', 'multiselect.async'];

export type TAdapter = <TFieldType extends FieldType, TDataShape extends DataShape>(
	props: RenderFieldProps<TFieldType, TDataShape>
) => JSX.Element;

export const Adapter: TAdapter = forwardRef<any, RenderFieldProps<any, any>>((props, ref) => {
	const { fieldType, ...rest } = props as any;
	const Component = adapterMap?.[fieldType] || DefaultComponent;

	const fieldRef = nonRefTypes.includes(fieldType) ? null : ref;

	return <Component {...(rest as any)} ref={fieldRef} />;
});
