import { forwardRef } from 'react';
import ReactSelectAsync, { Props } from 'react-select/async';

import { components } from './components';

type Option = { label: string; value: string };

export type MultiSelectAsyncProps = Props<Option, true>;

export const MultiSelectAsync = forwardRef<ReactSelectAsync<Option, true>, MultiSelectAsyncProps>((props, ref) => {
	return <ReactSelectAsync classNamePrefix='react-select' components={components} isMulti {...props} ref={ref} />;
});
