import { useFormContext } from 'react-hook-form';

import { FormFieldProps, FieldType, DataShape } from '../types';
import { RenderField } from '../render';
import { useFieldConditions, useFieldError } from '../hooks';

export const Field = <TFieldType extends FieldType, TDataShape extends DataShape>(
	props: FormFieldProps<TFieldType, TDataShape>
): JSX.Element => {
	const { conditions, valueAsNumber, name, ...rest } = props as any;

	const { register } = useFormContext();
	const error = useFieldError(props.name);
	const conditionsApply = useFieldConditions(props.name, conditions);

	if (!conditionsApply) {
		return null;
	}

	const fieldProps = register(name, { required: props.isRequired, valueAsNumber });

	return <RenderField {...fieldProps} error={error} {...rest} />;
};
