import classNames from 'classnames';

import { Box } from '@wp-plugins/adapters';

import { FormFieldProps, DataShape } from '../types';
import { FormField } from '../FormField';
import { useFieldConditions } from '../hooks';

export const Group = <TFieldType extends 'group', TDataShape extends DataShape>(
	props: FormFieldProps<TFieldType, TDataShape>
): JSX.Element => {
	const {
		after,
		before,
		conditions,
		defaultValue,
		groupClassName,
		groupItemProps,
		label,
		name: groupName,
		subFields,
	} = props;

	const conditionsApply = useFieldConditions(groupName, conditions);

	if (subFields?.length && conditionsApply) {
		return (
			<Box className='field-group-wrap'>
				<h5>{label}</h5>
				{before}
				<Box className={classNames('field-group-items', groupClassName)}>
					{subFields.map(({ name: fieldname, fieldType, ...rest }, i) => {
						const name = `${groupName}.${fieldname}`;
						const className = classNames('field-group-item', groupItemProps?.className, fieldname);

						return (
							<Box {...groupItemProps} className={className} key={name + i}>
								<FormField
									{...rest}
									defaultValue={defaultValue?.[fieldname as string]}
									fieldType={fieldType}
									name={name}
								/>
							</Box>
						);
					})}
				</Box>
				{after}
			</Box>
		);
	}
	return null;
};

export default Group;
