import { FormFieldProps, DataShape, FieldType } from '../types';
import { RenderRepeatable } from '../render';
import { useFieldConditions, useFieldError } from '../hooks';

export const Repeatable = <TFieldType extends FieldType, TDataShape extends DataShape>(
	props: FormFieldProps<TFieldType, TDataShape>
): JSX.Element => {
	const { conditions, name, ...rest } = props as any;

	const conditionsApply = useFieldConditions(props.name, conditions);
	const error = useFieldError(props.name);

	return conditionsApply && <RenderRepeatable name={name} error={error} {...rest} />;
};
