import { useFieldArray } from 'react-hook-form';

import { Text, FormControl, FormHelperText, FormLabel, IconButton, Flex, Button } from '@wp-plugins/adapters';
import { __ } from '@wp-plugins/i18n';
import { AddIcon, CloseIcon } from '@wp-plugins/icons';
import { SectionCard } from '@wp-plugins/components';

import { RenderRepeatableProps, DataShape, FieldType, RepeatableValue } from '../types';
import { FormField } from '../FormField';
import { ErrorMessage } from './ErrorMessage';

const DEFAULT_ITEM = { value: undefined };

export const RenderRepeatable = <TFieldType extends FieldType, TDataShape extends DataShape>(
	props: RenderRepeatableProps<TFieldType, TDataShape>
): JSX.Element => {
	const {
		addButtonLabel,
		after,
		afterRow: AfterRow,
		before,
		defaultItem = DEFAULT_ITEM,
		description,
		error,
		fieldType,
		initialLabel,
		label,
		name,
		renderAsSection,
		repeatableItemLabel,
		...rest
	} = props;

	const fieldArray = useFieldArray({ name });

	return (
		<div className='repeatable'>
			<FormControl isInvalid={Boolean(error)}>
				<FormLabel>{label}</FormLabel>
				{description ? <FormHelperText>{description}</FormHelperText> : null}
				<ErrorMessage name={name} />
			</FormControl>
			{!fieldArray.fields.length ? (
				<Text fontWeight='bold' as='span'>
					{initialLabel}&nbsp;
				</Text>
			) : null}
			{fieldArray.fields.map((field: RepeatableValue, index) => {
				if (!field) {
					return null;
				}
				const title = repeatableItemLabel?.(index) || `#${index + 1}`;

				const removeButton = (
					<IconButton
						aria-label='Remove Item'
						icon={<CloseIcon />}
						isRound
						// eslint-disable-next-line react-perf/jsx-no-new-function-as-prop
						onClick={() => fieldArray.remove(index)}
						size='sm'
						variant='ghost'
						ms={!renderAsSection ? '0.5em' : null}
					/>
				);

				const fieldName = `${name}.${index}.value`;

				const formField = (
					<FormField {...(rest as any)} defaultValue={field.value} name={fieldName} fieldType={fieldType} />
				);

				return renderAsSection ? (
					<SectionCard
						title={
							<Flex alignItems='center' justifyContent='space-between'>
								<Text>{title}</Text>
								{removeButton}
							</Flex>
						}
						key={field.id}
					>
						{before}
						{formField}
						{after}
						{AfterRow && <AfterRow name={fieldName} />}
					</SectionCard>
				) : (
					<Flex alignItems='baseline' key={field.id}>
						{formField} {removeButton}
					</Flex>
				);
			})}
			<Button
				mt='0.5em'
				className='add-item'
				// eslint-disable-next-line react-perf/jsx-no-new-function-as-prop
				onClick={() => fieldArray.append(defaultItem)}
				leftIcon={<AddIcon />}
			>
				{addButtonLabel || __('Add')}
			</Button>
		</div>
	);
};
